<template>
    <v-container class="right-panel-container">
        <nl-title title=""></nl-title>
        <v-expansion-panels class="right-panel" tile accordion multiple v-model="openedPanels">
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.load')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    vertical
                    :btns="loadObjBtn"
                    @loadObj="loadObj"
                    @loadObjLocal="loadObjLocal"></nf-buttons>
                    <input
                    ref="localFileInput"
                    type="file"
                    v-show="false"
                    @change="localFileLoaded"
                    />
                </v-expansion-panel-content>
            </v-expansion-panel>
            <v-expansion-panel>
                <v-expansion-panel-header>{{$t('m.i.save')}}</v-expansion-panel-header>
                <v-expansion-panel-content>
                    <nf-buttons
                    vertical
                    :btns="saveObjBtn"
                    @saveConfigJson="saveConfigJson"></nf-buttons>
                    <v-row no-gutters class="pa-1">
<p><b>config</b></p><p>Config<code>configPath.txt</code>config
<v-card light width="100%" class="ma-1 pa-1">
<pre>./model/config.json</pre>
</v-card></p>
<p>config
<v-card light width="100%" class="ma-1 pa-1">
<pre>
{
    "fileList": [
        "file_1.obj",
        "file_2.zip"
    ]
}</pre>
</v-card>
<code>file_1</code><code>file_2</code>config<code>/objects</code>.zip.obj.zip.obj</p>
<p>configconfig<code>configPath.txt</code></p>
                    </v-row>
                </v-expansion-panel-content>
            </v-expansion-panel>
            
        </v-expansion-panels>
    </v-container>
</template>

<script>
export default {
    data() {
        return {
            openedPanels: [0, 1],
            loadObjBtn: [
                {
                    label: this.$t("m.i.reloadNeuron"),
                    method: "loadObj"
                },
            ],
            saveObjBtn: [
                {
                    label: this.$t("m.i.saveConfig"),
                    method: "saveConfigJson"
                }
            ]
        }
    },
    methods: {
        loadObj() {
            var flag = confirm(this.$t("m.a.qDoReloadNeuron"));
			if(flag) {
                this.$emit("loadObj");
            }
        },
        loadObjLocal() {
            this.$refs["localFileInput"].click();
        },
        localFileLoaded(e) {
            this.$emit("loadObjLocal");

        },
        saveConfigJson() {
            this.$emit("saveConfigJson");
        }
    }

}
</script>

<style scoped>

</style>